.topToBtm {
  position: relative;
}

.iconStyle {
  position: fixed;
  bottom: 40px;
  right: 15px;
  z-index: 50;

  background-color: rgb(0, 176, 200);
  border-radius: 50%;
  height: 50px;
  width: 50px;
  color: #fff;
  cursor: pointer;
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.iconStyle:hover {
  background-color: rgb(152, 253, 131);
}
